import React, { Component } from 'react';
import { NavLink,withRouter } from "react-router-dom";
import './style.css'

function dealWidthShadow(path){
	if(path==="/"){
		return 'Header'
	}else if(path.match(/(\/faq|\/miners\/\w+)/)){
		return "black Header noShadow faq-bottom"
	}else {
		return "black Header"
	}
}
class Header extends Component {
	render() {
		return (
			<div className={dealWidthShadow(this.props.location.pathname)}>
			<div className="container">
			<NavLink to="/" className="logo">
			<i className={this.props.location.pathname==="/"?"logo-img blue":"logo-img"}></i>
			</NavLink>
			<ul className="navbar">
			<li>
			<NavLink to="/" activeClassName="active" exact className={this.props.location.pathname==="/"?"":"black"}></NavLink>
			</li>
			<li>
			<NavLink to="/blocks" activeClassName="active" exact className={this.props.location.pathname==="/"?"":"black"}></NavLink>
			</li>
			<li>
			<NavLink to="/miners" activeClassName="active" className={this.props.location.pathname==="/"?"":"black"}></NavLink>
			</li>
			<li>
			<NavLink to="/trainning" activeClassName="active" exact className={this.props.location.pathname==="/"?"":"black"}></NavLink>
			</li>
			<li>
			<NavLink to="/faq" activeClassName="active" exact className={this.props.location.pathname==="/"?"":"black"}></NavLink>
			</li>
			</ul>
			</div>
			</div>
			);
	}
}

export default withRouter(Header);


