import React, { Component } from 'react';
import './App.css';
import { BrowserRouter as Router, Route} from "react-router-dom";
import Header from './components/Header';
import Footer from './components/Footer';
import Home from './pages/Home';
import Blocks from './pages/Blocks';
import Miners from './pages/Miners';
import Miner_Stats from './pages/Miner_Stats';
import Trainning from './pages/Trainning';
import Faq from './pages/Faq';

class App extends Component {
  render() {
    return (
      <div className="App">
        <Router>
          <div>
            <Header />
            <Route exact path="/" component={Home} />
            <Route exact path="/blocks" component={Blocks} />
            <Route exact path="/miners" component={Miners} />
            <Route path="/miners/:address" component={Miner_Stats} />
            <Route exact path="/trainning" component={Trainning} />
            <Route exact path="/faq" component={Faq} />
            <Footer />
          </div>
        </Router>
      </div>
    );
  }
}

export default App;



