module.exports = {
	mixUp:function(arr1,arr2,flag){
		return arr2.reduce(function(result,current){
			let dup = false
			result.forEach(function(e,i){
				if(e[flag]===current[flag]){
					dup = true
				}
			})
			if(!dup){
				result.push(current)
			}
			return result
		},arr1)
	},
	mixUpSimple:function(arr1,arr2){
		return arr2.reduce(function(result,current){
			let dup = false
			result.forEach(function(e,i){
				if(e===current){
					dup = true
				}
			})
			if(!dup){
				result.push(current)
			}
			return result
		},arr1)
	},
	roundTo:function(n, digits) {
        if (digits === undefined) {
            digits = 0;
        }
        var multiplicator = Math.pow(10, digits);
        n = parseFloat((n * multiplicator).toFixed(11));
        var test =(Math.round(n) / multiplicator);
        return +(test.toFixed(digits));
    }
}


