import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import './style.css'
import Loading from '../../components/Loading'
import LoadingLite from '../../components/LoadingLite'
import api from '../../api'
import {mixUpSimple} from '../../utils'

function dateFmt(timestep,mark){
	function padlLeft(time){
		if(time>=10){
			return time
		}else {
			return "0"+time
		}
	}
	var date = new Date(timestep)
	if(mark === 1){
		return date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate()+" "+padlLeft(date.getHours())+":"+padlLeft(date.getMinutes())+":"+padlLeft((date.getSeconds()+1))
	}else if(mark === 1) {
		return date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate()+" "+padlLeft(date.getHours())+":"+padlLeft(date.getMinutes())
	}
	
}
class Blocks extends Component {
	constructor(props){
		super(props);
		this.renderBlocklist = this.renderBlocklist.bind(this)
		this.state = {
			minHeight:window.innerHeight-141,
			listIndex: 0,
			blockData:[],
			hasMore:false,
			loading:true,
			loadingLite:false
		}
	}
	renderBlocklist(){
		let _this = this;
		return new Promise((resolve,reject)=>{
			_this.setState({loadingLite:true})
			fetch(api.url+"blockList?index="+_this.state.listIndex).then(res => res.json()).then((result)=>{
				_this.setState({
					// blockData:_this.state.blockData.concat(result.result),
					blockData:mixUpSimple(_this.state.blockData,result.result),
					hasMore:result.hasMore,
					loading:false,
					listIndex:this.state.listIndex+1,
					loadingLite:false
				})
				resolve()
			},(error)=>{
				reject(error)
			})
		})
	}
	componentDidMount(){
		let _this = this;
		window.onresize = function(){
			_this.setState({minHeight:window.innerHeight-141})
		}
		this.renderBlocklist().then();
	}
	
	render() {
		return (
			<div className="blocks-container" style = {{minHeight:this.state.minHeight}}>
			<Loading loading={this.state.loading} />
			<div className="container">
			<table className="blockList">
			<thead>
			<tr>
			<th></th>
			<th></th>
			<th></th>
			<th></th>
			</tr>
			</thead>
			<tbody>
			<BlockList blockData={this.state.blockData} />
			</tbody>
			</table>
			<LoadingLite loading={this.state.loadingLite} event={()=>{this.renderBlocklist()}} hasMore={this.state.hasMore}/>

			{
				this.state.blockData.length===0 &&
				<div className="tipBox">
					
				</div>
			}
			</div>
			
			</div>

			);
	}
}
function BlockList(props){
	return props.blockData.map(function(e,i){
		let data = e.split(":")
		return (<tr key={i}>
			<td><a href={"//testnet-explorer.ulord.one/block/"+data[0]} target="_blank">{data[2]}</a></td>
			<td><a href={"//testnet-explorer.ulord.one/block/"+data[0]} target="_blank">{data[0]}</a></td>
			<td>{dateFmt(parseInt(data[4]),1)}</td>
			<td><Link to={"/miners/"+data[3].split(".")[0]}>{data[3].split(".")[0]}</Link></td>
			</tr>)
		})
	}

	export default Blocks


