import React, { Component } from 'react';
import { withRouter } from "react-router-dom";
import './style.css'

class Footer extends Component {
	render() {
		return (
			<div className={this.props.location.pathname==="/"?"footer transparent":"footer"}>
				<span className="copy">
					Copyright&nbsp;&copy;&nbsp;2018 UCA
				</span>
				<a href="mailto:service@ulorders.org" className="mail">
					service@ulorders.org
				</a>
			</div>
			);
	}
}

export default withRouter(Footer);


