import React, { Component } from 'react';
import './style.css'
import Charts from '../../components/Echarts'
import option from '../../e-option'
import { withRouter } from 'react-router-dom'
import Loading from '../../components/Loading'
import LoadingLiteEx from '../../components/LoadingLiteEx'
import Iter from '../../components/Iter'
import api from '../../api'
import {roundTo} from '../../utils'

function getReadableHashRateString(hashrate) {
	var i = 0;
	hashrate = parseFloat(hashrate)
	var byteUnits = [' H/s', ' KH/s', ' MH/s', ' GH/s', ' TH/s', ' PH/s'];
	while (hashrate >= 1000) {
		hashrate = hashrate / 1000;
		i++;
	}
	return hashrate.toFixed(2) + byteUnits[i];
}
function dateFmt(timestep, mark) {
	function padlLeft(time) {
		if (time >= 10) {
			return time
		} else {
			return "0" + time
		}
	}
	var date = new Date(timestep)
	if (mark === 1) {
		return date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate() + " " + padlLeft(date.getHours()) + ":" + padlLeft(date.getMinutes()) + ":" + padlLeft((date.getSeconds() + 1))
	} else if (mark === 2) {
		return date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate() + " " + padlLeft(date.getHours()) + ":" + padlLeft(date.getMinutes())
	}

}
function reDiff(e) {
	if (!e.online) {
		return '--'
	} else if (parseFloat(e.Diff) < 0) {
		return '--'
	} else {
		return Math.ceil(parseFloat(e.Diff) * 8192)
	}
}
class Miner_Stats extends Component {
	constructor(props) {
		super(props);
		option.legend.data[0] = ""
		option.series[0].name = ""
		this.getChartData = this.getChartData.bind(this)
		this.getData = this.getData.bind(this)
		this.getPayments = this.getPayments.bind(this)
		this.state = {
			minHeight: window.innerHeight - 141,
			chartsOptions: option,
			workerData: {
				loadingLite: false,
				result: [],
				currentPageIndex:0
			},
			paymentsData: {
				loadingLite: false,
				result: [],
				currentPageIndex:0
			},
			payouts: 0,
			immature: 0,
			loading: true,
			tabStatus: 0,
			inputNum:0
		}
	}
	componentDidMount() {
		let _this = this;
		window.onresize = function () {
			_this.setState({ minHeight: window.innerHeight - 141 })
		}
		this.getData().then(() => {
			Promise.all([this.getChartData(), this.getPayments()]).then(() => {
				this.setState({
					loading: false
				})
			}, (error) => {

			})
		}, (error) => {
			alert(error)
			this.props.history.push('/miners')
		})

	}

	getPayments=(pageIndex=0)=>{
		let _this = this
		return new Promise((resolve, reject) => {
			_this.setState({ paymentsData: { ...this.state.paymentsData, loadingLite: true, currentPageIndex:pageIndex}})
			fetch(api.url + "paymentsData?address=" + this.props.match.params['address'] + "&index=" + pageIndex).then(res => res.json()).then((result) => {
				if (result.message === "error") {
					reject(result.messageContent)

				}
				if(pageIndex===0){
					_this.setState({
						payouts: result.payouts,
						immature: result.immature,
						balances:result.balances,
						paymentsData: {...result.paymentsData,currentPageIndex:pageIndex,loadingLite:false}
					})
				}else {
					_this.setState({
						paymentsData: {...result.paymentsData,currentPageIndex:pageIndex,loadingLite:false}
					})
				}
				resolve()
			}, (error) => {

			})
		})

	}
	getData=(pageIndex=0)=>{
		return new Promise((resolve, reject) => {
			this.setState({ workerData: {...this.state.workerData, loadingLite: true, currentPageIndex: pageIndex} })
			fetch(api.url + "minerStats?address=" + this.props.match.params['address'] + "&index=" + pageIndex).then(res => res.json()).then((result) => {
				if (result.message === "error") {
					reject(result.messageContent)
					return
				}
				this.setState({
					workerData: {...result.workerStats,currentPageIndex:pageIndex,loadingLite:false}
				})
				resolve()

			}, (error) => {

			})

		})
	}
	getChartData() {
		return new Promise((resolve, reject) => {
			fetch(api.url + "chartsDataAddress?address=" + this.props.match.params['address']).then(res => res.json()).then((result) => {
				if (result.message === "error") {
					reject(result.messageContent)
				}
				option.series[0].data = result
				this.setState({ chartsOptions: option })
				resolve()
			}, (error) => {

			})
		})
	}
	render() {
		return (
			<div className="miners-container" style={{ minHeight: this.state.minHeight }}>
				<Loading loading={this.state.loading} />
				<div className="charts-container">
					<div className="container">
						<div className="charts-title"></div>
						<Charts option={this.state.chartsOptions} />
						<div className="paymentsStats">
							<div>
								<h1><br />(tULD)</h1>
								<p>{roundTo(this.state.payouts,8)}</p>
							</div>
							<div>
								<h1><br />(tULD)</h1>
								<p>{roundTo(this.state.balances,8)?roundTo(this.state.balances,8):"0"}</p>
							</div>
							<div>
								<h1><br />(tULD)</h1>
								<p>{roundTo(this.state.immature,8)}</p>
							</div>
						</div>
					</div>
				</div>
				<div className="wrapper">
					<div className="container tab">
						<div className="table-title">
							<a onClick={() => { this.setState({ tabStatus: 0 }) }} className={this.state.tabStatus === 0 ? "active" : ""}></a>
							<a onClick={() => { this.setState({ tabStatus: 1 }) }} className={this.state.tabStatus === 1 ? "active" : ""}></a>
							<Iter pageMax = {this.state.tabStatus===0?this.state.workerData.pageAmount:this.state.paymentsData.pageAmount} currentIndex={this.state.tabStatus===0?this.state.workerData.currentPageIndex:this.state.paymentsData.currentPageIndex} getPayments={this.getPayments} getData={this.getData} mode={this.state.tabStatus}/>

						</div>
					</div>
					<div className="workerContainer" style={{ display: this.state.tabStatus === 0 ? "block" : "none" }}>
						<div className="container">
							<LoadingLiteEx shown={this.state.workerData.loadingLite}/>
							<table className="workersList">
								<thead>
									<tr>
										<td></td>
										<td></td>
										<td></td>
										<td></td>
										<td></td>
									</tr>
								</thead>
								<tbody>
									<WorkerList workerData={this.state.workerData.result} />
								</tbody>
							</table>
						</div>
					</div>
					<div className="paymentsContainer" style={{ display: this.state.tabStatus === 0 ? "none" : "block" }}>
						<div className="container">
						<LoadingLiteEx shown={this.state.paymentsData.loadingLite}/>
							<table className="paymentsList">
								<thead>
									<tr>
										<td>ID</td>
										<td></td>
										<td></td>
									</tr>
								</thead>
								<tbody>
									
									<PaymentsList paymentsData={this.state.paymentsData.result} />
								</tbody>
							</table>
							{
								this.state.paymentsData.result.length === 0 &&
								<div className="tipBox">
									
								</div>
							}
						</div>
					</div>
				</div>
			</div>
		)
	}
}
function WorkerList(props) {
	const workerData = props.workerData
	return workerData.map(function (e, i) {
		return (<tr key={i}>
			<td>{e.name.split(".")[1] ? e.name.split(".")[1] : "NoName"}{!e.online ? <span className="offline">()</span> : null}</td>
			<td>{!e.online ? "--" : getReadableHashRateString(e.hashrate)}</td>
			<td>{getReadableHashRateString(e.hashrateAverage)}</td>
			<td>{reDiff(e)}</td>
			<td>{dateFmt(parseInt(e.lastSubmitTime,10), 1)}</td>
		</tr>)
	})
}
function PaymentsList(props) {
	const paymentsData = props.paymentsData
	return paymentsData.map(function (e, i) {
		if (e.txid !== 0) {
			return (<tr key={i}>
				<td><a href={"//testnet-explorer.ulord.one/tx/" + e.txid}>{e.txid}</a></td>
				<td>{e.amounts + " tULD"}</td>
				<td>{dateFmt(parseInt(e.time,10), 2)}</td>
			</tr>)
		}else {
			return undefined
		}
	})
}
export default withRouter(Miner_Stats)


