import React, { Component } from 'react';
import './style.css'

class LoadingLite extends Component {
      render() {
            return (this.props.hasMore) ? (
                  <div className="extendBox">
                        <a className="more" onClick={this.props.loading ? undefined : this.props.event}>
                              {this.props.loading ? (<div className="lds-css ng-scope">
                                    <div style={{ width: "100%", height: "100%" }} className="lds-ripple">
                                          <div>
                                          </div>
                                          <div>
                                          </div>
                                    </div>
                              </div>) : ""}
                        </a>
                  </div>


            ) : null;
      }
}

export default LoadingLite


