import React, { Component } from 'react';
import './style.css'

class Iter extends Component {
	render() {
		return (
			<div className="iter">
				<GeneratorIter pageMax={this.props.pageMax} currentIndex={this.props.currentIndex} getPayments={this.props.getPayments} getData={this.props.getData} mode={this.props.mode}/>
			</div>
		);
	}
}

export default Iter

function GeneratorIter(props) {
	if (props.pageMax < 8) {
		let arr = [], i = 0;
		while (i < props.pageMax) {
			arr[i] = i
			i++
		}
		return arr.map(function (e, i) {
			return (<div className={i === props.currentIndex ? "iterBox selected" : "iterBox"} key={i} onClick={()=>{props.mode===0?props.getData(i):props.getPayments(i)}}>{i + 1}</div>)
		})
	} else if (props.currentIndex < 5) {
		let arr = [], i = 0;
		while (i <= props.currentIndex+1) {
			arr[i] = i
			i++
		}
		return (<div>{arr.map(function (e, i){
			return (<div className={i === props.currentIndex ? "iterBox selected" : "iterBox"} key={i} onClick={()=>{props.mode===0?props.getData(i):props.getPayments(i)}}>{i + 1}</div>)
		})}<span className = "iterEcc" >...</span ><div className="iterBox" onClick={()=>{props.mode===0?props.getData(props.pageMax-1):props.getPayments(props.pageMax-1)}}>{props.pageMax}</div></div>)
	}else if(props.pageMax - props.currentIndex >=5){
		let arr = [], i = props.currentIndex-2;
		while (i <= props.currentIndex+3) {
			arr[i] = i
			i++
		}
		console.log(arr);
		return (<div><div className="iterBox" key={i} onClick={()=>{props.mode===0?props.getData(0):props.getPayments(0)}}>{1}</div><span className = "iterEcc" >...</span >{arr.map(function(e,i){
			return (<div className={e === props.currentIndex ? "iterBox selected" : "iterBox"} key={i} onClick={()=>{props.mode===0?props.getData(e):props.getPayments(e)}}>{e + 1}</div>)
		})}<span className = "iterEcc" >...</span ><div className="iterBox" onClick={()=>{props.mode===0?props.getData(props.pageMax-1):props.getPayments(props.pageMax-1)}}>{props.pageMax}</div></div>)
	} else if (props.pageMax - props.currentIndex < 5) {
		let arr = [], i = props.currentIndex - 1;
		while (i < props.pageMax) {
			arr[i] = i
			i++
		}
		return (<div><div className="iterBox" onClick={()=>{props.mode===0?props.getData(0):props.getPayments(0)}}>1</div><span className="iterEcc">...</span>{arr.map(function (e, i) {
			return (<div className={i === props.currentIndex ? "iterBox selected" : "iterBox"} key={i} onClick={()=>{props.mode===0?props.getData(i):props.getPayments(i)}}>{i + 1}</div>)
		})}</div>)
	}
}


