import React, { Component } from 'react';
import './style.css'
import { Link } from 'react-router-dom'
import Loading from '../../components/Loading'
import LoadingLite from '../../components/LoadingLite'
import api from '../../api'
function getReadableHashRateString(hashrate) {
	var i = 0;
	hashrate = parseFloat(hashrate)
	var byteUnits = [' H/s', ' KH/s', ' MH/s', ' GH/s', ' TH/s', ' PH/s'];
	while (hashrate >= 1000) {
		hashrate = hashrate / 1000;
		i++;
	}
	return hashrate.toFixed(2) + byteUnits[i];
}
function validateAddress(address) {
	if (address[0] === "U" && address.length === 34) {
		return true
	} else {
		return false
	}
}
class Miners extends Component {
	constructor(props) {
		super(props);
		this.handleSearch = this.handleSearch.bind(this)
		this.getMinersData = this.getMinersData.bind(this)
		this.state = {
			minHeight: window.innerHeight - 141,
			minersData: [],
			minerCount: 0,
			workerCount: 0,
			shares: 0,
			loading: true,
			hasMore: false,
			minerListIndex: 0,
			searchValue: "",
			loadingLite: false
		}
	}
	componentDidMount() {
		let _this = this;
		window.onresize = function () {
			_this.setState({ minHeight: window.innerHeight - 141 })
		}
		this.getMinersData().then();
	}
	handleSearch(e) {
		this.setState({
			searchValue: e.target.value
		})
	}
	getMinersData() {
		return new Promise((resolve, reject) => {
			this.setState({ loadingLite: true })
			fetch(api.url + "minerList?index=" + this.state.minerListIndex).then(res => res.json()).then((result) => {
				var resultData = result.result
				this.setState({
					minersData: this.state.minersData.concat(resultData),
					minerCount: result.minerCounts,
					workerCount: result.workerCounts,
					shares: result.shares,
					minerListIndex: this.state.minerListIndex + 1,
					hasMore: result.hasMore,
					loading: false,
					loadingLite: false
				})
				resolve()
			}, (error) => {
				reject(error)
			})
		})
	}
	handleEnter(e) {
		let keycode = e.keyCode || e.which
		if (keycode === 13) {
			if (validateAddress(this.state.searchValue)) {
				this.props.history.push("/miners/" + this.state.searchValue)
			}
		}
	}
	render() {
		return (
			<div className="miners-container" style={{ minHeight: this.state.minHeight }}>
				<Loading loading={this.state.loading} />
				<div className="container">
					<div className="minersDashboard">
						<span>{this.state.workerCount}</span>
						<span>{this.state.minerCount}</span>
						<span>shares{this.state.shares.toFixed(2)}</span>
						<input className="search-box" placeholder="" onChange={this.handleSearch} onKeyPress={(e) => { this.handleEnter(e) }} />
						<button className="btn-search" disabled={!validateAddress(this.state.searchValue)} onClick={() => { this.props.history.push("/miners/" + this.state.searchValue) }}></button>
					</div>
					<table className="minersList">
						<thead>
							<tr>
								<th></th>
								<th>Shares</th>
								<th></th>
								<th></th>
								<th></th>
							</tr>
						</thead>
						<tbody>
							<MinersList minersData={this.state.minersData} />
						</tbody>
					</table>
					<LoadingLite loading={this.state.loadingLite} event={() => { this.getMinersData() }} hasMore={this.state.hasMore} />
				</div>

			</div>
		)
	}
}
function MinersList(props) {
	const minersData = props.minersData
	return minersData.map(function (e, i) {
		return (<tr key={i}>
			<td><Link to={"/miners/" + e.name}>{e.name}</Link></td>
			<td>{e.roundShares.toFixed(2)}</td>
			<td>{e.roundShares ? (((e.roundShares + e.invalidShares) / (e.roundShares)).toFixed(2) * 100 + "%") : "100%"}</td>
			<td>{getReadableHashRateString(e.hashrate)}</td>
			<td>{e.workerCount}</td>
		</tr>)
	})
}
export default Miners


