import React, { Component } from 'react';
import Charts from '../../components/Echarts'
import Loading from '../../components/Loading'
import './style.css'
import option from '../../e-option.js'
import api from '../../api'

class Home extends Component {
	constructor(props) {
		super(props)
		this.getStaticData = this.getStaticData.bind(this);
		this.getChartsData = this.getChartsData.bind(this);
		this.state = {
			minerCounts: "",
			workerCounts: "",
			networkDiff: "",
			networkSols: "",
			totalHashrate: "",
			hashrateAverage: "",
			chartsData: option,
			loading: true
		}
	}
	getReadableHashRateString(hashrate) {
		var i = 0;
		hashrate = parseFloat(hashrate)
		var byteUnits = [' H/s', ' KH/s', ' MH/s', ' GH/s', ' TH/s', ' PH/s'];
		while (hashrate >= 1000) {
			hashrate = hashrate / 1000;
			i++;
		}
		return hashrate.toFixed(2) + byteUnits[i];
	}
	getChartsData() {
		return new Promise((resolve, reject) => {
			fetch(api.url + "chartsDataPool").then(res => res.json()).then((result) => {
				option.series[0].data = result
				this.setState({ chartsData: option })
				resolve()
			}, (error) => {
				reject(error)
			})
		})
	}
	getStaticData() {
		return new Promise((resolve, reject) => {
			fetch(api.url + "poolStats").then(res => res.json()).then((result) => {
				let { minerCounts, workerCounts, networkDiff, networkSols, totalHashrate, hashrateAverage } = result
				this.setState({
					minerCounts,
					workerCounts,
					networkDiff,
					networkSols,
					totalHashrate,
					hashrateAverage
				})
				resolve()
			}, (error) => {
				reject(error)
			})
		})
	}
	componentDidMount() {
		this.setState({ minHeight: window.innerHeight-181})
		Promise.all([this.getChartsData(), this.getStaticData()]).then(() => {
			this.setState({ loading: false })
		})
	}
	render() {
		return (
			<div className="home-container" style={{minHeight:this.state.minHeight}}>
				<Loading loading={this.state.loading} />
				<div className="tips">
					WebDDOS
			</div>
				<div className="dashboard">
					<div>
						<div>
							<h1>{this.getReadableHashRateString(this.state.totalHashrate)}</h1>
							<h3></h3>
						</div>
					</div>
					<div>
						<div>
							<h1>{this.getReadableHashRateString(this.state.hashrateAverage)}</h1>
							<h3></h3>
						</div>
					</div>
					<div>
						<div>
							<h1>{this.state.minerCounts}</h1>
							<h3></h3>
						</div>
					</div>
					<div>
						<div>
							<h1>{this.state.workerCounts}</h1>
							<h3></h3>
						</div>
					</div>
					<div>
						<div>
							<h1>{this.state.networkSols}</h1>
							<h3></h3>
						</div>
					</div>
					<div>
						<div>
							<h1>{parseFloat(this.state.networkDiff).toFixed(5)}</h1>
							<h3></h3>
						</div>
					</div>
				</div>
				<div className="charts-container">
					<Charts option={this.state.chartsData} />
				</div>
				<div className="info">
					<div className="info-row">
						<span>PPLNT</span><span>4</span>
					</div>
					<div className="info-row">
						<span>192.168.14.246</span><span>7200</span>
					</div>
				</div>
			</div>
		);
	}
}

export default Home;


